/*-------------------------
  Variables
-------------------------*/
/*-------------------------
  Typography
-------------------------*/
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,500;0,600;0,700;1,400&display=swap");
html {
  font-size: 10px;
  scroll-behavior: auto !important;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  line-height: 1.75;
  color: #343839;
  font-weight: 400;
  height: 100%;
  vertical-align: baseline;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  background-color: #ffffff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  color: #141414;
  line-height: 1.3;
  margin: 0 0 20px;
}

h1 {
  font-size: 32px;
  font-weight: 700;
}

h2 {
  font-size: 28px;
  font-weight: 700;
}

h3 {
  font-size: 24px;
  font-weight: 700;
}

h4 {
  font-size: 20px;
}

h5 {
  font-size: 18px;
}

h6 {
  font-size: 16px;
}

p {
  font-size: 16px;
  line-height: 1.75;
  margin: 0 0 15px 0;
}

/*-------------------------
  Default Styles
-------------------------*/
.container {
  max-width: 1140px;
  padding-left: 15px;
  padding-right: 15px;
}

.mainmenu-nav > ul, .header-top .header-items ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  display: inline-block;
  text-decoration: none;
}

img {
  max-width: 100%;
}

input {
  width: 100%;
  background-color: transparent;
  border: 1px solid #d4d4d4;
  border-radius: 4px;
  font-size: 14px;
  padding: 0 25px;
  color: #141414;
  height: 50px;
}
input:focus {
  outline: none;
  box-shadow: none;
}

/*-------------------------
  Header Styles
-------------------------*/
.header-top {
  padding: 20px 0;
}
.header-top .header-brand img {
  max-width: 150px;
}
.header-top .header-items ul {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin: 0 -10px;
}
.header-top .header-items ul li {
  margin: 0 10px;
}
.header-top .header-items ul .header-search {
  position: relative;
}
@media only screen and (max-width: 767px) {
  .header-top .header-items ul .header-search {
    display: none;
  }
}
.header-top .header-items ul .header-search .btn-search {
  border: none;
  background-color: transparent;
  color: #808080;
  font-size: 16px;
  position: absolute;
  top: 12px;
  left: 15px;
}
.header-top .header-items ul .header-search .btn-search:hover {
  color: #5dc2ca;
}
.header-top .header-items ul .header-search .search-input {
  padding-left: 50px;
  width: 500px;
}
@media only screen and (max-width: 991px) {
  .header-top .header-items ul .header-search .search-input {
    width: 100%;
  }
}
.header-top .header-items ul .login-btn {
  border: 1px solid #d4d4d4;
  padding: 10px 25px;
  color: #141414;
  border-radius: 4px;
  font-weight: 500;
  transition: 0.3s;
}
.header-top .header-items ul .login-btn:hover {
  background-color: #5dc2ca;
  border-color: #5dc2ca;
  color: #ffffff;
}
.header-top .header-items ul .mobile-menu-toggle {
  border: 1px solid #d4d4d4;
  padding: 4px 15px;
  color: #141414;
  border-radius: 4px;
  font-weight: 500;
  transition: 0.3s;
  font-size: 20px;
}
@media only screen and (min-width: 992px) {
  .header-top .header-items ul .mobile-menu-toggle {
    display: none;
  }
}
.header-top .header-items ul .mobile-menu-toggle:hover {
  background-color: #5dc2ca;
  border-color: #5dc2ca;
  color: #ffffff;
}

.header-mainmenu {
  border-top: 1px solid #ebecef;
  box-shadow: 0 10px 40px 0 rgba(0, 0, 0, 0.05);
}

.mainmenu-nav > ul {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 -30px;
}
.mainmenu-nav > ul > li {
  position: relative;
}
.mainmenu-nav > ul > li > a {
  font-size: 15px;
  font-weight: 500;
  color: #141414;
  padding: 0 30px;
  display: block;
  height: 70px;
  line-height: 70px;
  transition: 0.3s;
}
.mainmenu-nav > ul > li > a:hover {
  color: #5dc2ca;
}
.mainmenu-nav > ul li {
  position: relative;
}
.mainmenu-nav > ul li.has-submenu > a {
  position: relative;
}
.mainmenu-nav > ul li.has-submenu > a::after {
  content: "\f107";
  font-family: "Font Awesome 6 Free";
  font-weight: 600;
  font-size: 12px;
  position: absolute;
  right: 12px;
  top: 1px;
}
.mainmenu-nav > ul li.has-submenu .submenu {
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 90%;
  left: 0;
  background-color: #ffffff;
  z-index: 4;
  opacity: 0;
  visibility: hidden;
  min-width: 200px;
  padding: 15px 10px;
  box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.08);
  border-bottom: 1px solid #5dc2ca;
  transition: 0.3s;
}
.mainmenu-nav > ul li.has-submenu .submenu li a {
  font-size: 15px;
  color: #141414;
  padding: 5px 15px;
  display: block;
  transition: 0.3s;
  border-radius: 4px;
}
.mainmenu-nav > ul li.has-submenu .submenu li a:hover {
  background-color: #efefef;
}
.mainmenu-nav > ul li.has-submenu:hover .submenu {
  opacity: 1;
  visibility: visible;
  top: 100%;
}

@media only screen and (max-width: 991px) {
  .mainmenu-nav {
    width: 200px;
    padding: 30px;
    overflow-y: auto;
  }
  .mainmenu-nav > ul {
    display: block;
    margin: 0;
  }
  .mainmenu-nav > ul > li > a {
    height: auto;
    line-height: 1;
    padding: 15px 0;
  }
  .mainmenu-nav > ul li.has-submenu > a::after {
    right: 44px;
    top: 18px;
  }
  .mainmenu-nav > ul li.has-submenu .submenu {
    display: none;
    position: initial;
    opacity: 1;
    visibility: visible;
    min-width: 100%;
    box-shadow: none;
    border-bottom: none;
    transition: none;
    padding: 0 5px;
  }
  .mainmenu-nav > ul li.has-submenu .submenu li a {
    padding: 5px 0;
  }
  .mainmenu-nav > ul li.has-submenu .submenu li a:hover {
    background-color: transparent;
    color: #5dc2ca;
  }
}
/*-------------------------
  Footer Style
-------------------------*/
.footer-area {
  background-color: #f0f0f0;
}
.footer-area .footer-top {
  padding: 60px 0 10px;
}

.footer-widget {
  margin-bottom: 50px;
}
.footer-widget ul {
  list-style: none;
  padding: 0;
}
.footer-widget ul li {
  margin-top: 12px;
  margin-bottom: 12px;
}
.footer-widget ul li a {
  color: #343839;
  transition: 0.3s;
  position: relative;
}
.footer-widget ul li a:hover {
  color: #5dc2ca;
}
.footer-widget .support-list-item {
  margin-top: 25px;
}
.footer-widget .support-list-item li a {
  font-weight: 400;
}
.footer-widget .support-list-item li a i {
  padding-right: 8px;
}
.footer-widget .download-btn-group {
  margin-top: 20px;
}
.footer-widget .download-btn-group .app-link {
  display: flex;
  align-items: center;
}
.footer-widget .download-btn-group a {
  margin-right: 10px;
}
.footer-widget .download-btn-group a:last-child {
  margin-right: 0;
}

.copyright-area {
  background-color: #ebecef;
  text-align: center;
  padding: 16px 0;
}
.copyright-area .copyright-text p {
  margin-bottom: 0;
  font-size: 15px;
}

/*-------------------------
  Breadcrumb Styles
-------------------------*/
.breadcrum-area {
  padding: 30px 0;
  background-color: #ebecef;
}

.breadcrumb {
  margin-bottom: 0;
}
.breadcrumb .breadcrumb-item {
  color: #141414;
}
.breadcrumb .breadcrumb-item a {
  color: #808080;
  transition: 0.3s;
}
.breadcrumb .breadcrumb-item a:hover {
  color: #5dc2ca;
}
.breadcrumb .breadcrumb-item + .breadcrumb-item {
  padding-left: 10px;
}
.breadcrumb .breadcrumb-item + .breadcrumb-item::before {
  content: "\f105";
  font-family: "Font Awesome 6 Free";
  font-weight: 600;
  padding-right: 10px;
  font-size: 14px;
  top: 3px;
  position: relative;
}

.breadcrum-banner-area {
  background-image: url("../images/breadcrumb-bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  padding: 30px 0;
  z-index: 1;
}
.breadcrum-banner-area:before {
  content: "";
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}
.breadcrum-banner-area .banner-content .title {
  color: #ffffff;
  font-size: 40px;
}
.breadcrum-banner-area .banner-content .review-wrap .rating {
  background-color: #5dc2ca;
  display: inline-flex;
  align-items: center;
  color: #ffffff;
  padding: 6px 15px;
  border-radius: 4px;
}
.breadcrum-banner-area .banner-content .review-wrap .rating i {
  margin-right: 8px;
  position: relative;
  top: -1px;
}
.breadcrum-banner-area .banner-content .review-wrap .rating .avrg-rating, .breadcrum-banner-area .banner-content .review-wrap .rating i {
  font-size: 20px;
}
.breadcrum-banner-area .banner-content .review-wrap .rating .total-rating {
  margin-left: 10px;
  font-size: 15px;
}
.breadcrum-banner-area .banner-content .list-item {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-top: 30px;
}
.breadcrum-banner-area .banner-content .list-item li {
  color: #ffffff;
  margin: 12px 0;
  font-size: 18px;
  position: relative;
  padding-left: 35px;
}
.breadcrum-banner-area .banner-content .list-item li:before {
  content: "\f00c";
  font-family: "Font Awesome 6 Free";
  font-weight: 600;
  position: absolute;
  top: 0;
  left: 0;
}
.breadcrum-banner-area .breadcrumb {
  margin-bottom: 30px;
}
.breadcrum-banner-area .breadcrumb .breadcrumb-item {
  color: #ffffff;
}
.breadcrum-banner-area .breadcrumb .breadcrumb-item a {
  color: #d5d5d5;
}
.breadcrum-banner-area .breadcrumb .breadcrumb-item a:hover {
  color: #ffffff;
}
.breadcrum-banner-area .breadcrumb .breadcrumb-item + .breadcrumb-item::before {
  color: #d5d5d5;
}

/*-------------------------
  Services Styles
-------------------------*/
.service-area {
  padding: 100px 0;
}
.service-area .main-heading-title {
  font-weight: 500;
  margin-bottom: 30px;
}

.service-grid {
  border-radius: 4px;
}
.service-grid .thumbnail {
  overflow: hidden;
  border-radius: 4px;
  position: relative;
}
.service-grid .thumbnail img {
  border-radius: 4px;
  object-fit: cover;
  width: 100%;
  transition: 0.3s;
  transform: scale(1);
  transition: transform 2s cubic-bezier(0.2, 0.96, 0.34, 1);
}
.service-grid .content .title {
  text-align: center;
  margin-bottom: 30px;
  font-size: 16px;
  font-weight: 600;
}
.service-grid .content .title a {
  display: block;
  padding: 20px;
  color: #141414;
  transition: 0.3s;
}
.service-grid .content .title a:hover {
  color: #eb6ea6;
}
.service-grid:hover .thumbnail img {
  transform: scale(1.05);
}

.service-container-wrap {
  display: grid;
  grid-template-columns: 230px auto;
  gap: 40px;
}

.service-nav {
  border-right: 2px solid #ebecef;
}
.service-nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
  position: sticky;
  top: 10px;
}
.service-nav ul li {
  font-size: 15px;
  font-weight: 500;
}
.service-nav ul li a {
  color: #141414;
  padding: 8px 15px;
  border-radius: 4px;
  display: block;
  transition: 0.3s;
}
.service-nav ul li a.current, .service-nav ul li a:hover {
  background-color: #ebecef;
}
.service-nav ul li.current a {
  background-color: #ebecef;
}

.service-section {
  padding-bottom: 80px;
}
.service-section .section-title {
  font-weight: 600;
  margin-bottom: 30px;
}

.isotope-button {
  margin-bottom: 60px;
  text-align: center;
}
.isotope-button button {
  border: none;
  background-color: transparent;
  font-size: 18px;
  font-weight: 500;
  color: #343839;
  margin-right: 100px;
  padding: 2px 0;
  position: relative;
}
.isotope-button button:focus-visible {
  outline: none;
}
.isotope-button button:after {
  content: "";
  height: 2px;
  width: 0;
  background-color: #eb6ea6;
  position: absolute;
  bottom: 0;
  left: 0;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s;
}
.isotope-button button.active, .isotope-button button:hover {
  color: #141414;
}
.isotope-button button.active:after, .isotope-button button:hover:after {
  visibility: visible;
  opacity: 1;
  width: 100%;
}
.isotope-button button:last-child {
  margin-right: 0;
}

/*-------------------------
  Service Details Styles
-------------------------*/
.service-details-area {
  padding: 120px 0;
}

.service-sidebar {
  position: fixed;
  top: 30%;
  right: 50px;
  z-index: 5;
  min-width: 400px;
}
.service-sidebar .inner {
  width: 100%;
  background-color: #5dc2ca;
  border-radius: 4px;
  padding: 30px;
}
.service-sidebar .sidebar-service-list ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.service-sidebar .sidebar-service-list ul .service-btn {
  background-color: transparent;
  border: none;
  background-color: #ffffff;
  width: 100%;
  text-align: left;
  padding: 5px 20px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.service-slide-area {
  padding: 100px 0 70px;
}
@media only screen and (max-width: 991px) {
  .service-slide-area {
    padding: 60px 0 30px;
  }
}

.flex-heading {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
.flex-heading .section-title {
  margin-bottom: 10px;
}
.flex-heading .view-all-btn {
  color: #eb6ea6;
  transition: 0.3s;
}
.flex-heading .view-all-btn i {
  padding-left: 10px;
  transition: 0.3s;
}
.flex-heading .view-all-btn:hover {
  color: #5dc2ca;
}
.flex-heading .view-all-btn:hover i {
  padding-left: 5px;
}

.owl-nav {
  margin-top: 0;
}
.owl-nav button {
  position: absolute;
  top: 30%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  margin: 0;
  padding: 0;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  color: #5dc2ca !important;
  background: #ffffff !important;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.12);
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: none;
  transition: 0.3s;
}
.owl-nav button i {
  font-size: 18px;
}
.owl-nav button:hover {
  background: #5dc2ca !important;
  color: #ffffff !important;
}
.owl-nav button:focus {
  outline: none;
}
.owl-nav .owl-prev {
  left: -30px;
}
@media only screen and (max-width: 1199px) {
  .owl-nav .owl-prev {
    left: 0;
  }
}
.owl-nav .owl-next {
  right: -30px;
}
@media only screen and (max-width: 1199px) {
  .owl-nav .owl-next {
    right: 0;
  }
}

.section-heading-2 {
  margin-bottom: 30px;
}
.section-heading-2 .subtitle {
  display: block;
  padding-left: 40px;
  margin-bottom: 10px;
  position: relative;
  font-size: 14px;
}
.section-heading-2 .subtitle:before {
  content: "";
  width: 30px;
  height: 1px;
  background-color: #979797;
  position: absolute;
  top: 12px;
  left: 0;
}

.process-area {
  padding: 100px 0 60px;
}
@media only screen and (max-width: 991px) {
  .process-area {
    padding: 60px 0;
  }
}

.process-video {
  position: relative;
  text-align: center;
  margin-bottom: 40px;
}
.process-video img {
  height: 350px;
  border-radius: 8px;
  width: 100%;
  object-fit: cover;
  object-position: center;
}
.process-video .popup-icon {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
}

.process-setps-list {
  padding-left: 60px;
  position: relative;
}
@media only screen and (max-width: 991px) {
  .process-setps-list {
    padding: 0;
  }
}
.process-setps-list:before {
  position: absolute;
  content: "";
  width: 3px;
  height: 250px;
  top: 20px;
  left: 84px;
  background-color: #f7f8fa;
}
@media only screen and (max-width: 991px) {
  .process-setps-list:before {
    left: 24px;
  }
}
.process-setps-list .process-steps {
  position: relative;
  padding-left: 70px;
  margin-bottom: 50px;
}
.process-setps-list .process-steps:last-child {
  margin-bottom: 0;
}
.process-setps-list .process-steps .sl-number {
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 50%;
  background-image: linear-gradient(135deg, #23a999 1%, #00a1ba 52%, #006597);
  color: #fff;
  font-size: 24px;
  font-weight: 500;
  text-align: center;
}
.process-setps-list .process-steps .title {
  margin-bottom: 8px;
}
.process-setps-list .process-steps .process-text {
  font-size: 14px;
}

.blog-list-area {
  padding: 100px 0 60px;
}

.blog-meta {
  list-style: none;
  padding: 0;
  margin: 0;
  line-height: 1;
}
.blog-meta li {
  display: inline-block;
  font-size: 10px;
  position: relative;
  color: #b4b4b4;
}
.blog-meta li:after {
  content: "-";
  padding-left: 5px;
}
.blog-meta li a {
  color: #f3bb2c;
}
.blog-meta li:last-child:after {
  display: none;
}

.blog-list {
  margin-bottom: 40px;
  padding-bottom: 40px;
  border-bottom: 1px solid #ebebeb;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.blog-list .blog-thumb a {
  display: block;
}
.blog-list .blog-thumb img {
  height: 200px;
  width: 260px;
}
.blog-list .blog-content {
  flex: 1;
  padding-left: 30px;
}
.blog-list .blog-content .title {
  margin-bottom: 10px;
  margin-top: 10px;
  font-weight: 400;
}
.blog-list .blog-content .title a {
  color: #141414;
  transition: 0.3s;
}
.blog-list .blog-content .title a:hover {
  color: #eb6ea6;
}
.blog-list .blog-content .reading-time span {
  font-weight: 500;
  font-size: 14px;
}
.blog-list .blog-content p {
  font-size: 14px;
}
.blog-list .blog-content p:last-child {
  margin-bottom: 0;
}